@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.overview,
.security,
.saved,
.reports,
.profile {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.main-body {
    padding-top: 25px;
}

button {
    border: none;
    border-radius: 20px;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 0 solid rgba(0,0,0,.125);
    border-radius: 15px;
    box-shadow: 0 5px 10px 0 rgba(0,0,0,0.3);
}

.card-body {
    flex: 1 1 auto;
    min-height: 1px;
}

.card-rounded {
    width: 100%;
    
}

.profile {
    background-color: antiquewhite;
    border-radius: 6px;
    width: 100%;
    display: inline-block;
}

.profile_name {
    line-height: 18px;
    padding-left: 10px;
    padding-top: 15px;
}

.profile_name .name {
    text-transform: uppercase;
    font-size: 20px;
    font-weight: bold;
}

.profile_name .title {
    font-size: 14px;
    color: grey;
}

.lazy {
    padding: 2rem;
}

.profile_image img {
    width: 100%;
    object-fit:none; /* Do not scale the image */
    object-position: center; /* Center the image within the element */
    width: 100%;
    max-height: 280px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}

.profile_info{
    padding: 10px;
}

ul.profile_timeline {
    list-style-type: none;
    position: relative;
    margin-left: auto;
}


ul.profile_timeline > li {
    margin: 20px 0;
    align-items: center;
}


ul.profile_timeline .profile_timeline_desc {
    font-weight: bold;
}

.prof_icons {
    margin-right: 10px;
    font-size: 2rem;
}
.content {
    padding-top: 20px;
    max-width: 800px;
    margin: auto;
}

.card-header:first-child {
    border-radius: calc(.2rem - 1px) calc(.2rem - 1px) 0 0;
}
.card-header {
    border-bottom-width: 1px;
}
.card-header {
    padding: .75rem 1.25rem;
    margin-bottom: 0;
    color: inherit;
    background-color: #fff;
    border-bottom: 1px solid #e5e9f2;
}

.form-group {
    padding: 8px;
}

.btn_add {
    background-color: #fff;
    color: black;
}

button {
    border: 2px solid black;
    border-radius: 20px;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
}
